.cards{
    width: 80%;
    margin: 0 auto;
}
.products-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 1%;
  }
  
  .product-card-container {
    width: 25%;
    display: flex;
    /* justify-content: space-between; */
    margin: 10px;
  }
  
  .product-card {
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
    transition: all 0.3s ease-in-out;
    width: 20%;
    margin-bottom: 20px;
  }
  
  .product-card:hover {
    border: 1px solid #ccc;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    border-radius: 5px;
  }
  
  .product-image-container {
    height: 250px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: filter 0.3s ease-in-out;
  }
  
  .product-image-container:hover {
    filter: grayscale(10%) brightness(90%);
    transform: rotatey(360deg);
    transition: 6s;
  }
  
  .product-image {
    height: 100%;
    width: 100%;
    object-fit: contain;
  }
  
  .product-info {
    padding: 10px;
    text-align: center;
  }
  
  .product-title {
    font-size: 1.2rem;
    margin: 0 0 10px;
  }
  
  .product-price {
    margin: 0;
  }
  
  .product-center {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }