.login{
  /* margin: 30px 0; */
}
.form-container {
  width: 30%;
  margin-left: 35%;
  margin-right:auto ;
  border-radius: 0.75rem;
  background-color: rgb(231, 223, 255);
  padding: 2rem;
 
  color: rgb(2, 2, 2);
  display: flex;
  justify-content: center;
  flex-direction: column;
box-shadow: 1px 5px 10px 5px rgb(78, 53, 134);;
height: 60vh;
margin-bottom: 20px;
}

.title {
  text-align: center;
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 700;
}

.form {
  margin-top: 1.5rem;
}

.input-group {
  margin-top: 0.25rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.input-group label {
  display: block;
  color: rgb(20, 22, 24);
  margin-bottom: 4px;
}

.input-group input {
  width: 100%;
  border-radius: 0.375rem;
  border: 1px solid rgba(55, 65, 81, 1);
  outline: 0;
  background-color: rgb(255, 255, 255);
  /* padding: 0.75rem 1rem; */
  color: black;
  height: 30px;

}

.input-group input:focus {
  border-color: rgba(167, 139, 250);
}

.forgot {
  display: flex;
  justify-content: flex-end;
  font-size: 0.75rem;
  line-height: 1rem;
  color: rgb(0, 0, 0);
  margin: 8px 0 14px 0;
}

.forgot a,.signup a {
  color: rgb(0, 0, 0);
  text-decoration: none;
  font-size: 14px;
}

.forgot a:hover, .signup a:hover {
  text-decoration: underline rgba(167, 139, 250, 1);
}

.sign {
  display: block;
  width: 100%;
  background-color: rgba(167, 139, 250, 1);
  padding: 0.75rem;
  text-align: center;
  color: rgba(17, 24, 39, 1);
  border: none;
  border-radius: 0.375rem;
  font-weight: 600;
}

.social-message {
  display: flex;
  align-items: center;
  padding-top: 1rem;
}

.line {
  height: 1px;
  flex: 1 1 0%;
  background-color: rgba(55, 65, 81, 1);
}

.social-message .message {
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  color: rgb(0, 0, 0);
}

.social-icons {
  display: flex;
  justify-content: center;
}

.social-icons .icon {
  border-radius: 0.125rem;
  padding: 0.75rem;
  border: none;
  background-color: transparent;
  margin-left: 8px;
}

.social-icons .icon svg {
  height: 1.25rem;
  width: 1.25rem;
  fill: #131010;
}

.signup {
  text-align: center;
  font-size: 0.75rem;
  line-height: 1rem;
  color: rgb(0, 0, 0);
}
 
.form-container{
    margin-right: 50px;
    margin-top: 50px;
}

.forgot {
  margin-right: 80px;
  margin-top: 25px;
  display: flex;
  justify-content: space-between;
}
