@import url('https://fonts.googleapis.com/css2?family=Lobster+Two:ital@1&family=Poppins:wght@300&display=swap');
*{
  box-sizing: border-box;
}
body{ 
    padding: 0;
    margin: 0;
    overflow-x: hidden;
}


.wish{
    background-color: #f7f8fb;
    font-size: 1rem;
}
.wishlist, table{
    padding: 70px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 70%;
    margin: 0 auto;
}

.red{
  background-color: #ff324d ;
  width: 100%;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.red h2{
  padding-right: 10%;
  font-size: 30px;
  color: white;
}
.red div{
    background-color: white;
    width: 600px;
    height: 70px;
    display: flex;
    justify-content: space-between;
    border-radius: 15px;
}
.red div input,.red div button{
    outline: none;
    border: none;
    border-radius: 15px;
    padding: 0 15px;
    font-size: 1.2rem;
}


article.product{
  display: flex;
  width: 70%;
  margin: 0 auto;
  text-align: center;
  /* justify-content: center; */
  align-items: center;
  height: 100px;
}
article .f1{
  width: calc(100% / 6);
}
article .img{
   width: 130px;
   /* height: 100px; */
   margin: 40px 0 0 0 ;
}
article img{
   width: 100%;
   margin: 40px 0 0 0 ;
   /* height: 100px; */
}
article button{
  width: calc(100% / 6);
  height: 50px; 
  color:#ff324d ;
  background-color: white;
  border: none;
  border-radius: 15px;
}
.overflow{
  overflow-x:hidden ;
}

article button:hover{
  width: calc(100% / 6);
  height: 50px; 
  background-color:#ff324d ;
  color: white;
  border: none;
  border-radius: 15px;
}
.stock{
  background: green;
  width: fit-content;
  border-radius: 15px;
}
