*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.product{
  display: flex;
  justify-content: space-between;
  align-items:center ;
  width: 70%;
  margin: 0 auto;
  height: 350px;
}
.img{
  width: 200px;
  height: 200px;
  margin: 1%;
}
.product img{
  width: 100%;
  padding: 0 20px;
  margin: 20px 0;
}
