/*  RESPONSIVE WEB SITE */
/* Styles for extra small screens (less than 480px wide) */
@media (max-width: 480px) {
  .con{
    display: flex;
    align-items: center;
  }
  .contain2{
    flex-direction: column;
  }
  .contain2  .c1{
    flex-direction: column;
  }
  .abs{
    width: 50%;
    padding-left: 5px;
  }
  .abs p,.abs h1{
    font-size:.8rem;

  }
  .products-container{
    flex-direction: column;
  }
  .products-container .product-card{
    width: 100%;
  }
  footer{
    height: auto;
  }
  footer section{
    display: block;
  }
  .login .form-container{
    width: 100%;
    margin: 0 auto;
    margin-bottom: 10px;
  }
  .btn{
    margin-top: 0;
    margin-left: 130px;
    width: 1rem;
    height: 1rem;
    padding: 0;
  }
  .red h2 {
    padding:  0 10% 0 0;
  }

}

/* Styles for medium screens (between 576px and 768px wide) */
@media (min-width: 480px) and (max-width: 768px) {
  .con{
    display: flex;
    align-items: center;
  }
  .contain2{
    flex-direction: column;
  }
  .contain2  .c1{
    justify-content: center;
    padding-bottom: 2px;
    border-bottom:  1px dotted gray;
  }
  .abs{
    width: 50%;
    padding-left: 5px;
  }
  .abs p,.abs h1{
    font-size:.8rem;

  }
  .products-container{
    flex-direction: column;
  }
  .products-container .product-card{
    width: 100%;
  }
  footer{
    height: auto;
  }
  footer section{
    display: block;
  }

  .btn{
    margin-top: 0;
    margin-left: 130px;
  }
  .slide .btn{
    width: 5rem;
    font-size: smaller;
    right: 100px;
  }
  .login .form-container{
    width: 100%;
    margin: 0 auto;
    margin-bottom: 10px;
  }
  .border{
    flex-direction: column;
    width: 70%;
    margin: 0 auto;
    border: none;
  }
  .border .bord1{
    width: 100%;
    justify-content: space-between;
  }
  
}


/* Styles for large screens (between 768px and 992px wide) */
@media (min-width: 768px) and (max-width: 992px) {
  .contain2{
    width: 50%;
  }
  .contain2  .c1{
    flex-basis: 30%;
    flex-direction: column;
